@use 'variables' as v;

html {
    font-size: 62.5%;
    box-sizing: border-box;
}
*,
*:before,
*:after {
    box-sizing: inherit;
}
body {
    font-family: v.$main_font;
    font-size: 1.6rem;
    //only in gallery
    &.fix-body {
        overflow: hidden;
    }
    &.body-scroll {
        padding-top: 8.6rem; // fixed header when move
    }
}
p {
    
    color: v.$black;
    line-height: 1.5;
}
.content {
    width: 95%;
    max-width: 120%;
    margin: 0 auto;
}
a {
    text-decoration: none;
}
h1, h2 , h3 {
    margin: 0 0 5rem 0;
}
h1 {
    font-size: 4rem;
}
h2 {
    font-size: 3.6rem;
}
h3 {
    font-size: 3rem;
}
// Images 
img {
    max-width: 100%;
    width: 100%;
    height: auto;
    display: block;
}