@use '../base/mixins' as m;
@use '../base/variables' as v;

.about-festival {
    padding: 5rem 0; 
    @include m.tablet {
        @include m.grid(2, 5rem); //two columns in one fraction
    }
}
.content-festival {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-top: 5rem;
    @include m.tablet {
        margin-top: 0;
    }
    h2 {
        margin-bottom: 2rem;
    }
    .date {
        color: v.$purple;
        font-weight: 700;
    }
}