@use '../base/mixins' as m;
@use '../base/variables' as v;

.tickets {
    padding: 5rem 0;
    background-color: v.$gray;
    h3 {
        text-align: center;
        color: v.$white;
        margin-bottom: 2rem;
    }
    .ticket-content {
        @include m.tablet {
            @include m.grid(2,5rem);
        }
    }
}
.ticket {
    color: v.$black;

    .ticket-name {
        text-align: center;
        font-size:2.4rem;
        color: v.$white;
    }

    &-header,
    &-footer {
        padding: .5rem;
        p {
            text-align: center;
            color: v.$white;
            margin: 0;
        }
    }

    &-header{
        background-color: v.$black;
        p {
            text-transform: uppercase;
            font-size: 1.6rem;
        }
    }
    &-body {
        background-color: v.$white;
        ul {
            @include m.resetear-lista;
            li,
            :nth-child(odd) {
                background-color: v.$white;
                padding: .5rem;
                border-bottom: 1px solid #e1e1e1;
            }
        }
    }
    &-footer {
        background-color: v.$ticketventa;
        p {
            font-size: 2.4rem;
            font-weight: 700;
        }
    }
}